@charset "UTF-8";
@import url(./core/_variables.scss);
* {
  padding: 0;
  margin: 0; }

body {
  width: 100%;
  box-sizing: border-box; }

/* ESTILOS HEADER */
.header {
  width: 100%;
  height: 105px;
  box-sizing: border-box;
  position: fixed;
  align-content: center;
  text-transform: uppercase;
  background: rgba(9, 157, 141, 0.95);
  opacity: 95%;
  z-index: 2; }

/* NOMBRE EQUIPO */
.headername {
  width: 100%;
  height: 60px;
  text-align: center;
  opacity: 95%; }

/* MENÚ */
.teamname {
  line-height: 30px;
  font-family: "rubik", sans-serif;
  font-size: 16px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  color: white;
  padding-top: 15px; }

.menu {
  display: block;
  width: 100vw;
  height: 45px; }

.headermenu {
  display: flex;
  flex-direction: row;
  justify-items: center;
  justify-content: space-evenly;
  text-align: center;
  padding-left: 16px;
  padding-right: 17px;
  padding-top: 8px;
  padding-bottom: 7px;
  list-style: none;
  background-color: #14d9c4; }

/* LINKS MENÚ */
.team,
.aboutus,
.contact {
  line-height: 30px;
  font-family: "rubik", sans-serif;
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  text-decoration: none;
  color: white; }

.team {
  width: 56px;
  height: 30px; }

.aboutus {
  width: 121px;
  height: 30px; }

.contact {
  width: 80px;
  height: 30px; }

/* ESTILOS HERO */
.hero-image {
  width: 100vw;
  margin-top: 60px; }

/* LEMA HERO */
.motto {
  position: relative;
  margin-left: 15px;
  margin-right: 15px;
  top: -30px;
  background-color: white; }

.mottotext {
  padding: 15px;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal; }

/* ABOUT US */
.story,
.aboutUS__strengths,
.aboutUS__weaknesses,
.adalabers {
  padding: 30px 15px 30px 15px; }

.story {
  margin-top: -66px;
  padding-top: 45px; }
  @media all and (min-width: 768px) {
    .story {
      padding: 45px 45px 60px 45px; } }
  @media all and (min-width: 1200px) {
    .story {
      padding: 45px 300px 60px 300px; } }

.aboutUS__strengths {
  padding-top: 0;
  padding-bottom: 0; }
  @media all and (min-width: 768px) {
    .aboutUS__strengths {
      padding: 15px 15px 60px 45px;
      margin-top: -30px;
      flex: 1; } }
  @media all and (min-width: 1200px) {
    .aboutUS__strengths {
      padding: 15px 30px 60px 138px; } }

.aboutUS__weaknesses {
  padding-bottom: 45px; }
  @media all and (min-width: 768px) {
    .aboutUS__weaknesses {
      padding: 15px 45px 60px 15px;
      margin-top: -30px;
      flex: 1; } }
  @media all and (min-width: 1200px) {
    .aboutUS__weaknesses {
      padding: 15px 138px 60px 30px; } }

.aboutUS__triangleDown {
  width: 0;
  height: 0;
  border-left: 31px solid transparent;
  border-right: 31px solid transparent;
  border-top: 31px solid white;
  margin: -1px auto; }

.aboutUS {
  background-color: #b8b8b9; }
  @media all and (min-width: 1200px) {
    .aboutUS {
      margin: auto 138px; } }

.story__title {
  font-family: "Rubik";
  letter-spacing: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 45px;
  text-align: center;
  padding-bottom: 15px;
  font-weight: 500; }

.story__text,
.aboutUS__strengths--text,
.aboutUS__weaknesses--text {
  font-family: "Open Sans";
  text-align: center; }

.story__text {
  font-size: 24px;
  color: #14d9c4; }

.aboutUS__strengths--text,
.aboutUS__weaknesses--text {
  color: #099d8d;
  font-size: 22px; }

@media all and (min-width: 768px) {
  .aboutUS__wrapper {
    display: flex;
    flex-direction: row; } }

