.adalabers {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .adalabers__title {
    color: #099d8d;
    font-size: 24px;
    font-family: "Rubik";
    line-height: 45px;
    margin-bottom: 30px; }
